import React from 'react';
import Link from 'next/link';

const Navbar = () => (
    <nav className='navbar navbar-expand navbar-dark bg-warning mb-20'>
        <div className='container'>
            <Link href='/'><a className='navbar-brand'>Bitcoins Info</a></Link>
            <div className='collapse navbar-collapse'>
                <ul className='navbar-nav ml-auto'>
                    <li className='nav-item'>
                        <Link href='/'><a className='nav-link'>Inicio</a></Link>
                    </li>
                    <li>
                        <Link href='/nosotros'><a className='nav-link'>Nosotros</a></Link>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
);

export default Navbar;